import { Plugin, Notice } from "obsidian";
import { Memos } from "./memos";
import { MEMOS_VIEW_TYPE } from "./constants";
import { App } from "obsidian";
import addIcons from "./obComponents/customIcons";
import { outputResults } from "./obComponents/parseFile";
import "./helpers/polyfill";
import "./less/global.less";

declare global {
  interface Window {
    app: App;
  }
}

export default class MemosPlugin extends Plugin {
  async onload(): Promise<void> {

    this.registerView(
      MEMOS_VIEW_TYPE,(leaf) => new Memos(leaf, this),
    );

    addIcons();
    this.addRibbonIcon('changeTaskStatus', 'Memos', () => {
			new Notice('Open Memos Successfully');
      this.openMemos();
		});

    this.addCommand({
      id: "open-memos",
      name: "Open Memos",
      callback: () => this.openMemos(),
      hotkeys: [],
    });

    // this.app.workspace.onLayoutReady(this.onLayoutReady.bind(this));

    if (!this.app.workspace.layoutReady) {
      this.app.workspace.onLayoutReady(async () => outputResults());
     } else {  
      outputResults();
    }
  }

  onunload() {
    this.app.workspace.detachLeavesOfType(MEMOS_VIEW_TYPE);
    new Notice('Close Big Calendar Successfully');
  }

  // onLayoutReady(): void {
  //   if (this.app.workspace.getLeavesOfType(VIEW_TYPE).length) {
  //     return;
  //   }
  //   this.app.workspace.getRightLeaf(false).setViewState({
  //     type: VIEW_TYPE,
  //     active: true,
  //   });
  // }

  async openMemos() {
    const leaf = this.app.workspace.getLeaf(true);
    const neovisView = new Memos(leaf, this);
    await leaf.open(neovisView);
  }
}
